/* fuentes */
/* font-family: 'Source Sans Pro', sans-serif; */
/* font-family: 'Archivo Black', sans-serif; */

html * {
    margin: 0 ; 
    padding: 0 ;
    box-sizing: border-box 
}

.mainBox {
    min-height: 100vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.hero {
    justify-content: space-between;
    align-items: flex-start;
    flex: 100%; 
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 30% 45%;
    order: 1;
}

.containerHeader {
    display: flex;
    justify-content: space-between;
    flex: 100%;
}

nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 10vh;
    width: 30%;
    padding-left: 2.5%;
}

nav#navBar {
    margin-left: 5%;
    margin-top: 2vh;

}

nav#icon {
    padding-left: 18%;
}

nav a {
    font-family: 'Source Sans Pro', sans-serif;
    text-decoration: none;
    color: white;
}

img#user {
    height: 10vh;
    margin-top: 2vh;
}

.boxTitle {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80%;
}

.title {
    width: 35vw;
    margin-bottom: 7%;
}

img#heroImg {
    margin-top: 8%;
}

img#user {
    margin-right: 5vw;
}

.title h4 {
    font-family: 'Source Sans Pro', sans-serif;
    color: white;
    font-size: 1.3rem;
    width: 80%
}

.buttonSection {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 100%;
    height: auto;
    margin-top: 3.5%;
    margin-left: 3%;
    order: 2;
}

.buttonContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 42vh;
    flex: 1 1 100%;
    margin-right: 2%;
    margin-left: 6%;
    margin-bottom: 15%;    
}

.buttonContainer h4 {
    font-family: 'Source Sans Pro', sans-serif;
    color: rgb(238, 42, 132);
    font-size: 1.3rem;
    width: 80%
}

.buttonContainer h2, .boxCarrousel h2{
    font-family: 'Archivo Black', sans-serif;
    font-size: 2.5rem;
    color: rgb(245, 153, 0);
}

.boxCarrousel h2 {
    text-align: center;
    flex: 100%;
    margin-top: 10vh;
}

.buttonContainer button {
    width: 45%;
    background: rgb(255,178,41);
    background: linear-gradient(90deg, rgba(255,178,41,1) 0%, rgba(238,42,132,1) 100%);
    color: white;
    border-radius: 70px;
    text-decoration: none;
    border: none;
    padding: 16px 32px;
    text-align: center;
    font-size: 1rem;
    margin: 4px 2px;
    box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.363);

}

.mainCarrousel {
    display: flex;
    flex-direction: column-reverse;
    order: 4;
    flex: 100%;
}

.carrouselSection {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 90vh;
    
}

.titleSlider {
    align-self: center;
    text-align: center;
    font-family: 'Source Sans Pro', sans-serif;
    text-transform: uppercase;
    font-size: 1.5rem;
    color: white;
    margin-top: 4%;
    order: 3;
    width: 25%;
    padding: 1% 1%;
    border-radius: 70px;
    background: linear-gradient(90deg, rgba(255,178,41,1) 0%, rgba(238,42,132,1) 100%);
    box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.363);
}

.boxSlider {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60vw;
    flex-wrap: wrap;
}

.sliderImage {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 20vw;
    height: 35vh;
    margin: 1vh;
    border-radius: 5px;
    transition: 1.5s;
    box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.363);
}

.sliderImage p {
    color: white;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.8rem;
    text-shadow: 3px 3px 4px rgb(0, 0, 0);    
}

.carousel-control-prev, .carousel-control-next {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 20%;
    margin-top: 18.5%;
    width:5vw;
    height: 10vh;
    border-radius: 100% 100%;
    background: rgb(255,178,41);
    background: linear-gradient(90deg, rgba(255,178,41,1) 0%, rgba(238,42,132,1) 100%);
}

.carousel-control-next {
    margin-right: 20%;
}

.mainFooter {
    display: flex;
    justify-content: space-around;
    align-items: center;
    bottom: 0;
    background: linear-gradient(90deg, rgba(255,178,41,1) 0%, rgba(238,42,132,1) 100%);
    height: auto;
    flex: 1 1 100%;
    order: 5;
}

.boxFooter {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.allRightReserves {
    text-align: center;
    color: white;
    width: 50vw;
}

nav#linksFooter {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 20vw;
}

.socialMedia {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20%;
    padding-top: 1%;
}

.imgError {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.imgError img {
    width: 95%;
}



@media screen and (max-width: 768px) {
    .mainBox > *{
        order: 0;
    }

    .mainBox {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
    }
    
    .hero {
        min-width: 100%;
    }
    
    .containerHeader {
        width: 100%;
    }
    
    nav {
        width: 65%;
    }

    .boxTitle {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
    }
    
    .title {
        display: flex;
        flex-wrap: wrap;
        align-self: flex-end;
        width: 80vw;
        height: auto;
    }
    
    img#logo {
        width: 80vw;
        padding-top: 6%;
    }

    img#heroImg {
        width: 70vw;
        align-self: flex-start;
    }
    
    .title h4 {
        text-align: center;
        font-size: 1.6rem;
        width: 100%;
    }

    .buttonSection {
        margin-left: 0%;
        height: auto;
        display: flex;
        flex-wrap: wrap-reverse;
        justify-content: center;
        align-items: center;
    }

    .buttonSection img {
        width: 90vw;
        height: auto;
        margin-left: 4%;
        padding-bottom: 10%;
    }

    .buttonContainer {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 20vw;
        height: 30vh;
        padding-top: 10%;
    }
    
    .buttonContainer h4 {
        font-size: 1.6rem;
        width: 75vw;
        text-align: center;
        padding-bottom: 2%;
    }
    
    .buttonContainer h2 {
        text-align: center;
        font-size: 2.3rem;
        padding-bottom: 2%;
        width: 80vw;
    }

    .buttonContainer button {
        width: 100%;
        border-radius: 100px;
        padding: 8px 10px;
        text-align: center;
        font-size: 1.3rem;
        margin: 6px 4px;
    }

    .carrouselSection {
        height: auto;
        margin-bottom: 4%;
    }
    
    .titleSlider {
        width: 55%;
        margin-top: 4%;
        margin-bottom: 2%;
        font-size: 1.8rem;
    }
    
    .boxSlider {
        width: 80vw;
    }
    
    .sliderImage {
        width: 35vw;
        height: 40vh;
    }
    
    .sliderImage p {
        font-size: 2rem;
    }
    
    .carousel-control-prev, .carousel-control-next {
        display: none;
    }
    
    .mainFooter {
        background-color: rgb(245, 153, 0);
    }
    
    .boxFooter {
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    
    .allRightReserves {
        width: 100vw;
        font-size: 1.3rem;
    }
    
    nav#linksFooter {
        width: 100vw;
        font-size: 1.3rem;
    }
    
    .socialMedia {
        margin-top: 2%;
        display: flex;
    }

    .socialMedia img {
        width: 10vw;
    }
}

