        justify-content: center;
        align-items: center;
        width: 100%;
    }
    
    .hero {
        min-width: 100%;
    }
    
    .containerHeader {
        width: 100%;
    }
    
    nav {
        width: 45%;
    }

    .boxTitle {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
    }
    
    .title {
        display: flex;
        flex-wrap: wrap;
        align-self: flex-end;
        width: 80vw;
        height: auto;
    }
    
    img#logo {
        width: 80vw;
