    }

    .buttonSection {
        margin-left: 0%;
        height: auto;
        display: flex;
        flex-wrap: wrap-reverse;
        justify-content: center;
        align-items: center;
    }

    .buttonSection img {
        width: 80vw;
        height: auto;
        margin-left: 4%;
        padding-bottom: 10%;
    }

    .buttonContainer {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 20vw;
        height: 30vh;
        padding-top: 10%;
    }
    
    .buttonContainer h4 {
        font-size: 1rem;
        width: 75vw;
        text-align: center;
        padding-bottom: 2%;
    }
    
    .buttonContainer h2 {
        text-align: center;
        font-size: 1.3rem;
        padding-bottom: 2%;
        width: 55vw;
    }

    .buttonContainer button {
        width: 100%;
        border-radius: 100px;
        padding: 8px 10px;
        text-align: center;
        font-size: .8rem;
        margin: 6px 4px;
    }

    .carrouselSection {
        height: auto;
        margin-bottom: 4%;
    }
    
    .titleSlider {
        width: 80%;
        margin-top: 4%;
        margin-bottom: 2%;
        font-size: 1.3rem;
    }
    
    .boxSlider {
        width: 80vw;
    }
    
    .sliderImage {
        width: 50vw;
        height: 50vh;
    }
    
    .sliderImage p {
        font-size: 1.5rem;
    }
    
    .carousel-control-prev, .carousel-control-next {
        display: none;
    }
    
    .mainFooter {
        background-color: rgb(245, 153, 0);
    }
    
    .boxFooter {
        display: flex;
        flex-wrap: wrap-reverse;
        width: 100%;
    }
    
    .allRightReserves {
        width: 100vw;
        font-size: 0.8rem;
    }
    
    nav#linksFooter {
        width: 100%;
        font-size: 0.9rem;
    
    }
    
    .socialMedia {
        margin-top: 2%;
        display: flex;
    }

    .socialMedia img {
        width: 10vw;
    }

}

/* CITIES */
.mainCities {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.headerCities {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    top: 0;
    background: linear-gradient(90deg, rgba(255,178,41,1) 0%, rgba(238,42,132,1) 100%);
    height: 55vh;
    width: 100%;
    order: 1;
}

.citiesBox {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding-top: 4%;
    padding-bottom: 4%;
}

.photoGrid {
    width: 85%;
    margin-top: 2%;
    margin-bottom: 2%;
    background-repeat: no-repeat;
    background-position: 50% 35%;
    height: 50vh;
    transition: height 1s ease 0s;
}

.photoGrid:hover {
    height: 60vh;
}